.ScreenWrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow-y: auto; }
  .ScreenWrapper.pageTransition-enter {
    -webkit-transform: translateX(100%);
        -ms-transform: translateX(100%);
            transform: translateX(100%); }
  .ScreenWrapper.pageTransition-enter.pageTransition-enter-active {
    -webkit-transform: translateX(0);
        -ms-transform: translateX(0);
            transform: translateX(0);
    -webkit-transition: -webkit-transform 750ms ease;
    transition: -webkit-transform 750ms ease;
    -o-transition: transform 750ms ease;
    transition: transform 750ms ease;
    transition: transform 750ms ease, -webkit-transform 750ms ease; }
  .ScreenWrapper.pageTransition-leave {
    -webkit-transform: translateX(0);
        -ms-transform: translateX(0);
            transform: translateX(0); }
  .ScreenWrapper.pageTransition-leave.pageTransition-leave-active {
    -webkit-transform: translateX(-100%);
        -ms-transform: translateX(-100%);
            transform: translateX(-100%);
    -webkit-transition: -webkit-transform 750ms ease;
    transition: -webkit-transform 750ms ease;
    -o-transition: transform 750ms ease;
    transition: transform 750ms ease;
    transition: transform 750ms ease, -webkit-transform 750ms ease; }



