.EventsMap {
  width: 100%;
  height: 100%;
  position: relative; }

.EventsMap__overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 500;
  background-color: rgba(255, 255, 255, 0.5);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center; }

.EventsMap__overlay__viewButton {
  background-color: red;
  color: #FFFFFF;
  padding: 10px 20px;
  display: inline-block;
  border-radius: 20px; }

.EventsMap__exitFullscreen {
  position: absolute;
  top: 30px;
  right: 30px;
  z-index: 500;
  background-color: #FFFFFF;
  font-size: 14px;
  padding: 5px 10px;
  border-radius: 15px;
  -webkit-box-shadow: 0 1px 5px rgba(0, 0, 0, 0.05);
          box-shadow: 0 1px 5px rgba(0, 0, 0, 0.05); }



